<template>
    <div class="card" @click="routertoPlayListPage(SongInfo.id)">
        <div class="img-box">
            <img :src="SongInfo.picUrl" alt="">
            <span class="number">
                <i class="fa fa-headphones icon"></i>{{playCountParse(SongInfo.playCount)}}
            </span>
        </div>
        <p class="text-box">{{SongInfo.name}}</p>
          
    </div>
</template>

<script>
    export default {
        name: 'SongList',

        props: {
            SongInfo: {
                type: Object,
                default: () => {
                    return {
                        alg: "featured",
                        canDislike: false,
                        copywriter: "编辑推荐：我爱的人温柔善良 我不能一身戾气",
                        highQuality: false,
                        id: 924680166,
                        name: "[华语速爆新歌] 最新华语音乐推荐",
                        picUrl: "https://p2.music.126.net/lV4DVxCfWzQ-5Ri7v-vZCg==/109951164963138172.jpg",
                        playCount: 806496510,
                        trackCount: 30,
                        trackNumberUpdateTime: 1588521600000,
                        type: 0
                    }
                    
                }
            }
            
            
        },

        methods: {
            playCountParse(n) {

                if (n > 100000000) return (n / 100000000).toFixed(1) + '亿';

                if (n > 10000) return (n / 10000).toFixed(1) + '万';

            },

            routertoPlayListPage(id) {
                this.$router.push({path: 'playlist', query: {id: id}})
            }
        }
    }
</script>

<style lang="less" scoped>
    .card {
        flex-basis: 30%;
        flex-grow: 1;
        .img-box {
            position: relative;
            margin: 0 2px;
            img {
                width: 100%;
                display: block;
            }
            >.number {
                font-size: 12px;
                position: absolute;
                top: 3px;
                right: 5px;
                color: #fff;
                >.icon {
                    padding-right: 5px;
                    
                }
            }
        }
        .text-box {
            //必须结合的属性，将对象作为弹性伸缩盒子模型显示
            display: -webkit-box;
            //控制文本的行数
            -webkit-line-clamp: 2;
            //必须结合的属性，设置或检索伸缩盒对象的子元素的排列方式
            -webkit-box-orient: vertical;
            overflow: hidden;
            padding: 6px 2px 0 6px;
            min-height: .3rem;
            line-height: 1.2;
            font-size: 0.8em;
            margin: 0 0 8px 0;
        }
    }
</style>