<template>
  <div class="home">
      <HomeNav></HomeNav>
      <div class="recommend">
          <Title>推荐音乐</Title>
          <div class="songlist">
              <SongList v-for="(item, index) in randomRomSong()" :key="index" :SongInfo="item"></SongList>
          </div>
      </div>

      <div class="newest">
          <Title>最新音乐</Title>
          <div class="newSongIten">
              <NewSong v-for="(item, index) in newSong" :key="index" :songItem="item" @translate-song="$emit('translate-song', $event)" :currentSong="currentSong"></NewSong>
          </div>
      </div>

      
      
  </div>
</template>

<script>

import Title from '@/components/Title.vue'
import SongList from '@/components/SongList.vue'
import HomeNav from '@/components/HomeNav.vue'
import NewSong from '@/components/NewSong.vue'
// import PlayBar from '@/components/PlayBar'


export default {
    name: 'Home',

    components: {
      // HelloWorld
      Title,
      SongList,
      HomeNav,
      NewSong,
      // PlayBar
    },

    props: {
      currentSong: Object
    },

    data() {
        return {
          romSong: [],
          newSong: [],
        };
    },

    methods: {
        getRomSong() {

            // this.axios.get('https://music.kele8.cn/personalized')
            // .then((data) => {
            //     console.log('data ==>', data)
            // })
            // .catch((err) => {
            //   console.log('err ==>', err);
            // })

            this.axios({
                method: 'GET',
                url: 'https://music.kele8.cn/personalized'
            }).then(response => {
                // 如果数据正确
                // console.log(response);
                
                this.romSong = response.data.result;
                // console.log('this.romSong ==>', this.romSong);

                // 将数据放进本地存储
                window.localStorage.setItem('romSong', JSON.stringify({
                // 过期时间
                  expire: Date.now() + 2 * 60 * 60 * 1000,
                  result: response.data.result
                }))
                
            }).catch(err => {
                // console.log('err ==>', err);
            })

        },

         getNewSong() {
          this.axios({
              method: 'GET',
              url: 'https://music.kele8.cn/personalized/newsong'  
          }).then(response => {
              // 如果数据正确
              // console.log('response ==>', response);

              this.newSong = response.data.result;
              // console.log('this.newSong ==>', this.newSong);

              // 将数据放进本地存储
              window.localStorage.setItem('newSong', JSON.stringify({
                // 过期时间
                expire: Date.now() + 1 * 60 * 60 * 1000,
                result: response.data.result
              }))
              
          }).catch(err => {
              // console.log('err ==>', err);
          })
        },

        randomRomSong() {

            return [...this.romSong].slice(0, 6)

            // var arr = [...this.romSong];

            // var newArr = [];

            // for (let i = 0; i < 6; i++) {
              
            //   var r = Math.floor(Math.random() * arr.length);
            //   newArr.push(...arr.splice(r, 1))

            // }
            // return newArr;

        },
    },

    created() {

        // this.getRomSong();

        const localRomSongData = JSON.parse(window.localStorage.getItem('romSong'));

        const localNewSongData = JSON.parse(window.localStorage.getItem('newSong'));

        if (localRomSongData && localRomSongData.expire > Date.now()) {
          // 存在且还没有过期
          this.romSong = localRomSongData.result;

        } else {
          // 已经过期
          this.getRomSong();

        }

        if (localNewSongData && localNewSongData.expire > Date.now()) {
          // 存在且还没有过期
            this.newSong = localNewSongData.result;

        } else {
          // 已经过期
          this.getNewSong();

        }

    },

}
</script>

<style lang="less" scoped>
      .songlist {
        display: flex;
        flex-wrap: wrap;
      }
</style>
